var that;
export default {
	data() {
		return {
			list: [],
			nomore: false,
			nolist: false,
			type: 0,
			page: 1
		};
	},
	methods: {
		del(e) {
			let id = e.currentTarget.dataset.id; 
			let index=e.currentTarget.dataset.index;
			uni.showModal({
				title: '提示',
				content: '确定要删除',
				cancelText: '取消',
				confirmText: '确定',
				success(res) {
					if (res.confirm) {
						that.$request
							.request({
								url: that.api.live.del_notice_live,
								data: {
									id: id
								},
								Toast:'请稍等...',
								method:"get"
							})
							.then(res => { 
								that.list.splice(index,1)
								that.$alertok("删除成功！");
								
							});
					}
				}
			});
		},
		getlist() {
			var that = this;
			that.$request
				.request({
					url: that.api.live.my_live_notice,
					method: 'GET',
					data: {
						page: that.page
					}
				})
				.then(res => { 
					if (res.list.length) {
						for (var i = 0; i < res.list.length; i++) {
							var time = res.list[i].play_time;
							var newDate = new Date(time);
							var newDateTime = that.happenTimeFun(newDate);
							res.list[i].time = newDateTime;
						}
					}
					console.log(res);
					if (that.page == 1) {
						that.list = res.list;
						that.page = that.page + 1;
					}else{
						if (res.list.length > 0) {
							if (that.page != 1) {
								that.list = that.list.concat(res.list);
							}
							that.page = that.page + 1;
						} else {
							if (that.page != 1) {
								if (!that.nomore) {
									that.nomore = true;
								}
							} else {
								that.nolist = true;
							}
						}
					}
					
				});
		},
		happenTimeFun(num) {
			//时间戳数据处理
			var num = num * 1000;
			let date = new Date(num);
			//时间戳为10位需*1000，时间戳为13位的话不需乘1000
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? '0' + MM : MM; //月补0
			let d = date.getDate();
			d = d < 10 ? '0' + d : d; //天补0
			let h = date.getHours();
			h = h < 10 ? '0' + h : h; //小时补0
			let m = date.getMinutes();
			m = m < 10 ? '0' + m : m; //分钟补0
			let s = date.getSeconds();
			s = s < 10 ? '0' + s : s; //秒补0
			return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
		},
		tpinfo(e) { 
			uni.navigateTo({
				url: '../yugaoInfo/yugaoInfo?id=' + e.currentTarget.dataset.id
			});
		}
	},
	onLoad() {
	 that = this;
		that.getlist();
	}, 
	onReachBottom() { 
		that.getlist();
	},
	onPullDownRefresh() {
		that.page=1;
		that.list=[];
		that.getlist();
	}
};
